#header {
    background-color: #353B48;
    font-family: 'Roboto', sans-serif;
    position: relative;

    padding: 10px 40px;
    z-index: 1;
    font-size: 2.5em;
}

.header-links {
    position: relative;
    display: inline;

    margin-right: 0.5em;
    line-height: 1em;
    vertical-align: middle; 
}

.header-links a {
    margin-left: 1em;
    margin-right: 1em;
    color: #fff;
    text-decoration: none;
}

.right-content {
    position: relative;
    display: inline;

    /* margin-left: 14em; */
    right: 20%;
    line-height: 1em;
    text-align: right;
    vertical-align: middle;
}

.user-menu {
    display: inline;
}

.user-menu {
    width: 6%;
    border-radius: 50%;
    vertical-align: middle;
    margin-left: 0.5em;
}

.logo {
    position: relative;
    width: 6%;
    border-radius: 50%;
    vertical-align: middle;
    margin-left: 85%;  
}